import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'

Vue.use(VueRouter)

const routes = [{
		path: '/',
		redirect:'/home'
		
	},
	{
		path: '/home',
		name: 'Home',
		component: () => import('../pages/home.vue'),
		meta:{
			istabbar:true,
			barId:'0',
		}
	},
	{
		path: '/faxian',
		name: 'faxian',
		component: () => import('../pages/faxian.vue'),
		meta:{
			istabbar:true,
			barId:'1',
		}
	},
	{
		path: '/news',
		name: 'news',
		component: () => import('../pages/news.vue'),
		meta:{
			istabbar:true,
			barId:'3',
		}
	},
	{
		path: '/mine',
		name: 'mine',
		component: () => import('../pages/mine.vue'),
		meta:{
			istabbar:true,
			barId:'4',
		}
	},
	{
		path: '/tangshidetail',
		name: 'tangshidetail',
		component: () => import('../pages/tangshidetail.vue')
	},
	{
		path: '/tpl',
		name: 'tpl',
		component: () => import('../pages/tpl.vue')
	},
]

const router = new VueRouter({
	routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.istabbar) {
		store.dispatch('common/setCommonIsTabbar',{isTabBar:true,activeBar:to.meta.barId})
	}else{
		store.dispatch('common/setCommonIsTabbar',{isTabBar:false})
	}
	next()
})

export default router
