import request from '../../../utils/request.js'
import listData from "../../../components/tang300.js";
export default {
	async getTangshiDetail({commit},data){
		let resdata = '';
		let url = 'http://jx.onapp.top/jsapi/tangshidetail';
				url = 'https://o400l85452.imdo.co/jsapi/tangshidetail';
		await request({
			url:url,
			method:'get',
			params:data,
		}).then((res)=>{
			resdata = res.data;
		})
		return resdata;
	},
	async getTangshiList({commit},data){
		commit('SET_TANGSHILIST',listData.result)
	},
}