<template>
	<div class="tabbar">
		<div class="circle"></div>
		<ul class="item_list">
			<li v-for="(item,index) in tabbarData" :key="index" @click="goBarPage(item,index)" class="item" :class="{'item_circle':index==2,'active':activeBar==index,}">
				<span v-if="index!=2">{{item.text}}</span>
				<div v-if="index==2"><span>+</span></div>
			</li>
		</ul>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'
	export default {
		name: 'TabBar',
		props: {
			msg: String
		},
		computed:{
			...mapGetters('common', {
				tabBar: 'tabBar',
			}),
		},
		data() {
			return {
				tabbarData: [{
					text: '首页',
					icon: '',
					router: '/home',
				}, {
					text: '发现',
					icon: '',
					router: '/faxian',
				}, {
					text: '发布',
					icon: '',
					router: '/home',
				}, {
					text: '消息',
					icon: '',
					router: '/news',
				}, {
					text: '我的',
					icon: '',
					router: '/mine',
				}],
				activeBar: 0,
			}
		},
		created() {
			this.activeBar = this.tabBar.activeBar;
		},
		watch:{
			
		},
		methods: {
			goBarPage(item,index) {
				if (index != 2) {
					this.activeBar = index;
				}
				if(this.$route.fullPath == item.router){return;}
				this.$router.replace({
					path: item.router
				})
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
	.tabbar {
		position: fixed;
		//top: 60px;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 1rem;
		background-color: #fff;
		filter: drop-shadow(0px -1px 0px #ccc);
		z-index: 999;

		.item_list {
			position: relative;
			z-index: 2;
			height: 100%;
			display: flex;
			list-style: none;
			justify-content: space-between;
			align-items: center;
			text-align: center;
			font-size: .32rem;
			.item {
				flex: 1;
				color: palevioletred;
			}

			.active {
				color: darkmagenta;
				font-weight: bold;
			}

			.item_circle {
				position: relative;
				height: 100%;

				div {
					position: absolute;
					width: 1rem;
					height: 1rem;
					background-color: #ee20d2;
					border-radius: 50%;
					left: 50%;
					transform: translateX(-50%);
					top: -.02rem;
					display: flex;
					align-items: center;
					justify-content: center;
					color: #fff;
					font-size: .8rem;
				}
			}
		}

		.circle {
			width: 1.2rem;
			height: 1.2rem;
			border-radius: 100%;
			position: absolute;
			z-index: 1;
			top: -.1rem;
			left: 50%;
			transform: translateX(-50%);
			background-color: #fff;
		}
	}
</style>
